body{
    font-family: arial, sans-serif;
}
.card1 {
   
    padding: 40px 0px 20px 0px;
    width: 100%;
 
    background-color: white;
    box-shadow: 0 2px 6px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 10px;
   
  }

  table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
    color: black;
    font-weight: bold;
    font-size: 20px;
    text-align: center
  }
  td{
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
    color: black;
    /* text-align: center */
  }
  
  tr:nth-child(even) {
    background-color: #F2F2F2;
  }
  .present:hover, .present:focus{
      background-color: #5CB85C;
  }
  .absent:hover,  .absent:focus-within{
      background-color: red;
  }
  .late:hover, .late:focus{
      background-color: yellow;
  }
  .profile{
      width: 50px;
      margin-left: 0px;
      margin-top: 0px;
  }
  .breadcrumb{
    font-family:"arial, sans-serif";
    color: black;
    font-size: 20px;
  }
  .button-style{
      margin-left: 180px;
      width: 70%;
      font-weight: 700;
  }
  .card-body1{
      height:100%;
  }
